import React, { Component } from 'react'
import { Button, Layout, Modal } from 'antd'
import Login from '../login/Login'

export default class Header extends Component {
  constructor (p) {
    super(p)
    this.state = {
      visible: false,
      confirmLoading: false
    }
  }

  showModal = () => {
    this.setState({
      visible: true,
    })
  }
  handleOk = () => {
    this.setState({
      confirmLoading: true,
    })
  }
  handleCancel = () => {
    console.log('Clicked cancel button')
    this.setState({
      visible: false,
    })
  }

  render () {
    return (
      <Layout.Header style={{
        display:'flex',
        flexDirection:'row',
        justifyContent:'space-between',
        alignItems:'center'
      }}>
        <div/>
        <Login/>
      </Layout.Header>
    )
  }
}


